@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;800&display=swap);
body {
  background-image: url(/static/media/notesbackground.c9ff6c26.png);
  background-repeat: no-repeat;
  background-size: cover;
  font-family: "Montserrat";
}

.fullbody {
  background-image: url(/static/media/notesbackground.c9ff6c26.png);
  background-repeat: no-repeat;
  background-size: cover;
  overflow: auto;
}
.loader-container {
  width: 110%;
  margin: -8px;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  background: rgba(204, 218, 224, 0.834);
  z-index: 1;
}

.spinner {
  width: 64px;
  height: 64px;
  border: 8px solid;
  border-color: #3d5af1 transparent #3d5af1 transparent;
  border-radius: 50%;
  -webkit-animation: spin-anim 1.2s linear infinite;
          animation: spin-anim 1.2s linear infinite;
}

@-webkit-keyframes spin-anim {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin-anim {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.fullbuton {
  position: fixed;
  width: 60px;
  height: 60px;
  bottom: 40px;
  border: none;
  right: 40px;
  background-color: rgb(164, 208, 248);
  color: #fff;
  border-radius: 50px;
  text-align: center;
  box-shadow: 1px 1px 1px #999;
}

.main {
  max-width: 960px;
  margin: 0px auto;
  padding: 0px 15px;
  min-height: 100vh;
}

.header {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0px;
}

.notes__title {
  color: #fff;
  font-size: 38px;
  font-family: "Montserrat";
  font-weight: 400;
}

.notes {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(3, minmax(300px, 1fr));
}

@media screen and (max-width: 900px) {
  .notes {
    grid-template-columns: repeat(2, minmax(300px, 1fr));
  }
}

@media screen and (max-width: 500px) {
  .notes {
    grid-template-columns: repeat(1, minmax(300px, 1fr));
  }
  .header {
    flex-direction: column;
    justify-content: center;
  }
  .hr {
    display: none;
  }
  .altug {
    margin-top: -10px;
    display: none;
  }
}

.note {
    background: rgba(255, 255, 255, 0.1);
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    box-shadow: inset -6px -4px 2px rgba(255, 255, 255, 0.03);
    border-radius: 15px;
    border: 1.5px solid rgba(255, 255, 255, 0.326);
    color: #fff;
    padding: 15px;
    min-height: 140px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    word-wrap: break-word;
}

.note__footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;
}

.note textarea {
    white-space: pre-wrap;
    background: transparent;
    border: none;
    color: #fff;
    resize: none;
    font-size: 18px;
}

.note textarea:focus {
    outline: none;
}

.note textarea::-webkit-input-placeholder {
    color: #fff;
}

.note textarea::placeholder {
    color: #fff;
}

.note__save {
    background: transparent;
    transition: 0.1s ease-in-out;
    border: 1.5px solid #fff;
    border-radius: 10px;
    color: #fff;
    padding: 4px 10px;
    font-size: 13px;
    cursor: pointer;
}

.note__save:hover {
    background: #fff;
    border: 1.5px solid #fff;
    color: #4b1589;
}

.note__delete {
    cursor: pointer;
}

.note__delete:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.char__progress {
    background-color: #fff!important;
}
